/**
 * @module
 * @exports ready
 * @description The core dispatcher for the dom ready event javascript.
 *
 * @since 2.8.0
 *
 */

import { ready, applyBrowserClasses } from '@gravityforms/utils';

import common from 'common';

/**
 * @function bindEvents
 * @description Bind global event listeners here.
 *
 * @since 2.8.0
 *
 */

const bindEvents = () => {};

/**
 * @function init
 * @description The core dispatcher for init across the codebase.
 *
 * @since 2.8.0
 *
 */

const init = () => {
	// apply browser classes

	applyBrowserClasses();

	// initialize global events

	bindEvents();

	// initialize modules

	common();

	console.info(
		'GravityFlow Theme: Initialized all javascript that targeted document ready.'
	);
};

/**
 * @function domReady
 * @description Export our dom ready enabled init.
 *
 * @since 2.8.0
 *
 */

const domReady = () => {
	ready( init );
};

export default domReady;
